.tickerContainer {
	padding: 16px;
	background: var(--light-background-bg-secondlayer);
	border-radius: 12px;

	display: flex;
	justify-content: space-between;

	min-width: 612px;
	height: 96px;
}
.imgAndName {
	display: flex;
	gap: 16px;
}
.tickerImage {
	width: 60px;
	height: 60px;
	transform: translateY(2px);
}
.tickerHeader {
	display: flex;
	gap: 9px;
	margin-bottom: 4px;
}
.tickerFigi {
	font-size: 24px;
	font-weight: 600;
	line-height: 36px; /* 150% */
	color: var(--light-controls-stroke-controlsstroke-accent);
	padding-top: 1px;
}
.tickerCompanyName {
	color: var(--light-text-icons-text-secondary, #64748b);
	font-size: 20px;
	font-weight: 400;
	line-height: 28px; /* 140% */
	padding-top: 6px;
}
.tickerDetails {
	color: var(--light-text-icons-text-secondary);
	font-size: 16px;
	font-weight: 500;
	line-height: 24px; /* 150% */
	text-transform: uppercase;
}
.tickerProfitContainer {
	display: flex;
}
.tickerTotal {
	color: var(--light-text-icons-text-primary, #1d2023);
	text-align: right;
	font-size: 24px;
	font-weight: 600;
	line-height: 36px; /* 150% */
	margin-bottom: 4px;
	padding-top: 1px;
}
.tickerProfit {
	color: var(--light-text-icons-text-success);
	text-align: right;
	font-size: 16px;
	font-weight: 500;
	line-height: 24px; /* 150% */
	text-transform: uppercase;
}
