/* portfolio-summary */
.portfolio_summary {
	display: flex;
}
.total_info {
	display: flex;
	gap: 33px;
}
.header {
	min-width: 334px;
}
.total_value {
	font-size: 16px;
	font-weight: 500;
	line-height: 24px; /* 150% */
	text-transform: uppercase;
	color: var(--light-text-icons-text-secondary);
	margin-bottom: 12px;
}
.total_price {
	font-size: 60px;
	font-weight: 600;
	line-height: 80px; /* 133.333% */
	letter-spacing: -1.14px;
	color: var(--light-text-icons-text-primary);
	margin-bottom: 15px;
}
.income {
	display: flex;
	justify-content: space-between;
	margin-bottom: 32px;
}
.income p {
	font-size: 20px;
	font-weight: 400;
	line-height: 28px; /* 140% */
	color: var(--light-text-icons-text-secondary);
}
.income_profit {
	display: flex;
	gap: 8px;
}
.income_fix {
	font-size: 20px;
	font-weight: 400;
	line-height: 28px; /* 140% */
	color: var(--light-text-icons-text-primary);
	transform: translate(-3px, 0px);
}
.income_percent {
	font-size: 20px;
	font-weight: 500;
	line-height: 28px;
	color: var(--light-text-icons-text-success);
}
.income_percent::before {
	content: '(';
}
.income_percent::after {
	content: ')';
}
.income_percent::before,
.income_percent::after {
	font-weight: 400;
	color: var(--light-text-icons-text-secondary);
}
.composition_title {
	font-size: 20px;
	font-weight: 500;
	line-height: 28px; /* 140% */
	color: var(--light-text-icons-text-primary);
	margin-bottom: 12px;
}
.bar {
	width: 100%;
	height: 12px;
	border-radius: 10px;

	display: flex;
	gap: 1px;
	overflow: hidden;
	margin-bottom: 17px;
}
.bar div:hover {
	cursor: url('/src/assets/images/portfolioOverview/touch.png'), auto;
}
.infoTypeContainer {
	display: flex;
	flex-direction: column;
	gap: 8px;
}
.infoType {
	display: flex;
	justify-content: space-between;
	height: 22px;
}
.colorAndName {
	display: flex;
	gap: 8px;
}
.colorCube {
	width: 12px;
	height: 12px;
	border-radius: 4px;
}
.typeName {
	transform: translateY(-1px);
}
.percent {
	transform: translateY(-1px);
}

.divider {
	width: 1px;
	height: 428px;
	background: #e7eaee;
}
.timeLine {
	transform: translate(-796px, 25px);
	display: flex;
	background-color: #e7eaee;
	height: 36px;
	border: 1px solid #d0d5dd;
	border-radius: 20px;
}
.timeLine button:nth-child(1) {
	color: var(--light-text-inverted);
	background: var(--light-controlsbg-primary-default);
	width: 55px;
	padding: 7px 16px;
	border-radius: 20px;
	color: var(--light-text-inverted);

	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
}
.timeLine button:nth-child(2) {
	width: 59px;
	background-color: #e7eaee;
	color: var(--light-text-primary, #1d2023);
	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
}
.timeLine button:nth-child(3) {
	width: 68px;
	background-color: #e7eaee;
	color: var(--light-text-primary, #1d2023);
	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
}
.timeLine button:nth-child(4) {
	width: 53px;
	background-color: #e7eaee;
	border-radius: 20px;
	color: var(--light-text-primary, #1d2023);
	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
}
