.wrapper {
	padding-top: 11px;
	padding-bottom: 40px;
}
.includes_header {
	display: flex;
	justify-content: space-between;
	margin-bottom: 20px;
}
.includes_header h2 {
	font-size: 28px;
	font-weight: 600;
	line-height: 42px; /* 150% */
	color: var(--light-text-icons-text-primary);
}
.includes_btns {
	display: flex;
	gap: 12px;
}
.includes_btns button {
	padding: 8px 16px;
	border-radius: 100px;
	border: 1px solid var(--light-controlsstroke-default);
	background-color: #00000000;
	height: 38px;

	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
	color: var(-light-text-icons-text-primary);
}
.includes_btns button:nth-child(1) {
	width: 53px;
	background: var(--light-controlsbg-primary-default);

	font-size: 16px;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
	color: var(--light-text-inverted);
	border: 0;
}
.includes_btns button:nth-child(2) {
	width: 86px;
}
.includes_btns button:nth-child(3) {
	width: 83px;
}
.includes_btns button:nth-child(4) {
	width: 95px;
}
.includes_btns button:nth-child(5) {
	width: 87px;
}
.includes_btns button:nth-child(6) {
	width: 119px;
}
.includes_btns button:nth-child(7) {
	width: 85px;
}
.tickers_list {
	display: grid;
	grid-auto-flow: column;
	grid-template-rows: repeat(3, 1fr);
	column-gap: 24px;
	row-gap: 20px;
}
