.header {
	border-bottom: 1px solid var(--light-divider-divider);
	background: var(--light-bg-transparent);

	height: 80px;
}
.header .container {
	display: flex;
	gap: 116px;
}
.nav {
	display: grid;
	grid-template-columns: 648px 273px;
	grid-template-rows: 62px;
	gap: 56px;
	margin-top: 17px;
}
.logo {
	width: 156px;
	height: 31.781px;
	margin-left: -1px;
	margin-top: 28px;
}
.menu__links {
	display: grid;
	grid-template-columns: 82px 110px 138px 106px 115px 97px;

	font-size: 16px;
	font-style: normal;
	font-weight: 500;
	line-height: 22px; /* 137.5% */
}
.menu__links img {
	width: 24px;
	height: 24px;
	transform: translate(4px, -2px);
}
.menu__link_container {
	padding-top: 14px;
	padding-left: 13px;
	padding-right: 12px;
}
.menu__links div:nth-child(1),
.menu__links div:nth-child(2) {
	padding-left: 17px;
	padding-right: 16px;
}
.menu__links div:nth-child(2) {
	position: relative;
}
.menu__links div:nth-child(3),
.menu__links div:nth-child(6) {
	display: flex;
}
.menu__links div:nth-child(2) a {
	color: var(--light-text-link-default);
}
.menu__links div:nth-child(2)::before {
	content: '';
	display: block;
	width: 110px;
	height: 2px;
	background-color: var(--light-text-link-default);
	position: absolute;
	bottom: 0;
	left: 0;
}
.menu__link_container a {
	color: var(--light-text-primary);
}
.user_menu {
	display: flex;
	gap: 8px;
	align-items: center;
	height: 52px;
}
.user_menu img {
	width: 24px;
	height: 24px;
}
.user_menu > img {
	width: 40px;
	height: 40px;
	padding: 8px;
}
.user_info {
	display: flex;
	gap: 8px;
	padding: 12px 20px;
}
.user_info p {
	font-size: 20px;
	font-weight: 500;
	line-height: 28px; /* 140% */
}
.user_info img:nth-child(1) {
	transform: translate(0px, 2px);
}
.user_info p:nth-child(2) {
	transform: translate(1px, 0px);
}
.user_info img:nth-child(3) {
	transform: translate(1px, 2px);
}
