/* portfolio_header */
.portfolio_header {
	display: flex;
	justify-content: space-between;

	margin-bottom: 32px;
}
.portfolio_header__text h1 {
	font-size: 36px;
	font-style: normal;
	font-weight: 600;
	line-height: 48px; /* 133.333% */

	margin-bottom: 1px;
}
.portfolio_header__text p {
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	line-height: 22px; /* 137.5% */
}
.portfolio_header__btns {
	display: flex;
	gap: 12px;
}
.btns_header {
	background: var(--light-controls-background-controlsbg-secondary-default);
	border-radius: 12px;
}
.btn_refresh {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 8px;
	width: 201px;
	height: 52px;
}
.btn_refresh img {
	width: 24px;
	height: 24px;
}
.btn_refresh p {
	font-size: 20px;
	font-style: normal;
	font-weight: 500;
	line-height: 28px; /* 140% */
}
.btn_more {
	width: 52px;
	height: 52px;
}
.btn_more img {
	width: 24px;
	height: 24px;
}
